.section-slider__wrapper {
  position: relative;
}

.section-slider__wrapper--absolute {
  position: absolute;
}

//     
// ( ,   ,   slic )
.section-slider {
  .slick-arrow {
    top: unset;
    left: unset;
    right: unset;
    bottom: unset;
    transform: unset;
    width: 32px;
    height: 32px;
    z-index: 2;
    &::before {
      color: transparent;
    }
  }
}

.section-slider__icon {
  width: 32px;
  height: 32px;
  z-index: 1;
  position: absolute;
}

//
//   ,   
//

.section-slider--white-arrow {
  .slick-arrow {
    bottom: -52px;

    @include tablet {
      bottom: -64px;
    }
  }
}
.section-slider__icon--white {
  color: #ffffff;
  fill: currentColor;
  transition: var(--transition-color);
  bottom: -48px;

  @include tablet {
    bottom: -56px;
  }
}

//
//   ,     c
//

.section-slider--black-arrow {
  .slick-arrow {
    bottom: -48px;
    @include tablet {
      bottom: -56px;
    }
  }
}
.section-slider__icon--black {
  color: #000000;
  fill: currentColor;
  transition: var(--transition-color);
  bottom: -52px;
  @include tablet {
    bottom: -64px;
  }
  @include desktop {
    bottom: -72px;
  }
}

